import React from 'react';
import {NavLink} from 'react-router-dom';

const styleNavLink = {
    fontWeight: "bold"
}

const Header = () => {
    return (
        <header>
            <h1>Quizz Game</h1>
            <NavLink to="/" activeStyle={styleNavLink} exact>Dashboard</NavLink>
        </header>
    )
}

export default Header;